@tailwind base;
@tailwind components;
@tailwind utilities;

header ul li {
  @apply px-4;
}

.bg-primary{
   background: rgb(12, 71, 122);
   color: white;
}

.bg-secondary{
  background:rgb(8, 23, 56);
}

.mob-nav{
  @apply fixed top-10 left-0 bg-gray-500 w-full h-1/3;
}

.mob-nav li{
  @apply w-full h-full text-center border-b-2;
}

.project-align{
  @apply absolute left-0 right-0 bottom-0 top-0 bg-primary opacity-0 duration-500 hover:opacity-100;
}

.btn{
  @apply bg-primary px-5 py-2 font-bold hover:border-2 border-white rounded;
}

html{
  scroll-behavior: smooth;
}
